.home{
    border: 1px solid grey;
    border-radius: 10px;
    padding: 10px;
}

.square:hover {
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}

.square:not(:empty):hover {
    background: red;
}

.square:empty:hover {
    background: green;
}

.square {
    background: rgb(100, 100, 100);
    color: black;
    border: 1px solid #999;
    border-radius: 4px;
    font-size: 2em;
    font-weight: bold;
    line-height: 1em;
    height: 1em;
    width: 1em;
    margin-right: -1px;
    margin-top: 0px;
    margin-bottom: 0px;
    padding: 0;
    float: left;
    display: inline-table;
    text-align: center;
}


.game {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
    font-size: 25px;
}

.game-board {
    display: inline-block;
}

.game-info {
    width: 6em;
    display: block;
}

.game-info h2 {
    margin-top: 10px;
    margin-bottom: 30px;
    font-size: 0.8em;
}

.game-info button {
    font-size: 0.75em;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}

::-webkit-scrollbar {
    background-color: rgb(55, 55, 65);
}

::-webkit-scrollbar-thumb {
    background-color: rgb(45, 45, 55);
}

.game-info ul {
    max-height: 150px;
    background-color: rgb(45, 45, 55);
    padding-left: 0px;
    overflow-y: auto;
    overflow-x: hidden;
}

#historyArrow {
    display: inline;
    margin-left: 10px;
    font-size: 1em;
    visibility: hidden;
}

#historyBtnLi {
    list-style-type: none;
    font-size: 0.5em;
    text-align: center;
}

#historyBtnLi div {
}

#historyBtnLi button {
    background-color: rgb(70, 70, 70);
    border: none;
    border-bottom: 1px solid rgb(60, 60, 70);
    border-radius: 0px;
    margin: 0;
    margin-bottom: 1px;
    height: auto;
    width: 100%;
    padding: 5px;
}

#historyBtnLi:hover #historyArrow {
    visibility: visible;

}

.topNavigation {
    font-size: 20px;
    border-bottom: 1px solid grey;
    width: 100%;
    margin-top: 0px;
    margin-bottom: 50px;
    text-align: center;
}

.topNavigation ul {
    padding-left: 0;
    list-style-type: none;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 3px;
    display: inline-block;
}

.topNavigation li{
    display: inline-block;
}

.topNavigation button {
    font-size: 1em;
    margin-left: 15px;
    margin-right: 15px;
}

@media screen and(max-width: 50px){
    .topNavigation li{
        display: block;
        width: 100%;
    }

    .topNavigation button{
        width: 100%;
    }
}
body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background-color: rgb(50, 50, 60);
    color: rgb(170, 170, 170);
    text-align: center;
    min-width: -webkit-min-content;
    min-width: -moz-min-content;
    min-width: min-content;
}

button {
    color: rgb(170, 170, 170);
    background: rgb(80, 80, 80);
    border-radius: 5px;
    border: 1px solid rgb(30, 30, 30);
    height: 40px;
    margin-bottom: 5px;
}

button:hover {
    -webkit-transform: scale(1.05, 1.05);
            transform: scale(1.05, 1.05);
}


button:active {
    -webkit-transform: scale(0.95, 0.95);
            transform: scale(0.95, 0.95);
    border: 2px solid rgb(30, 30, 30);
}

:focus {
    outline: 0;
}
